  0% {
    -moz-box-shadow: 0 0 0 0 rgba(62, 100, 255, 0.4);
    -webkit-box-shadow: 0 0 0 0 rgba(62, 100, 255, 0.4);
    box-shadow: 0 0 0 0 rgba(62, 100, 255, 0.4);
